    <!-- ÂØºËà™Ê†è -->
    <NavBar />
    
    <!-- ‰∏ªÊ†áÈ¢ò -->
    <div class="main-header">
      <h1 class="title">ÊóÖË°å‰∏ªËèúÂçï</h1>
    </div>
    
    <!-- Ê¨¢ËøéËØ≠ -->
    <div class="welcome">
      Ê¨¢ËøéÊù•Âà∞ÊóÖË°åËÆ°Âàí‰∏é‰∫§ÊµÅÂπ≥Âè∞ÔºÅ<br>
      ÂèëÁé∞Á≤æÂΩ©Ë°åÁ®ãÔºåËÆ∞ÂΩïÁæéÂ•ΩÊóÖÈÄîÔºåÂíåÊúãÂèã‰∏ÄËµ∑ÂàÜ‰∫´ÊóÖË°åÊïÖ‰∫ã„ÄÇ
    </div>
    
    <!-- LoadingÁä∂ÊÄÅ -->
    <div v-if="loading" class="text-center py-5">
      <div class="spinner-border text-primary" role="status">
        <span class="visually-hidden">Âä†ËΩΩ‰∏≠...</span>
      </div>
    </div>
    
    <!-- Ëó§ËîìÂºèÂ∏ÉÂ±Ä -->
    <div v-else class="vine-container">
      <!-- ‰∏≠Èó¥ÁöÑËó§ËîìÁ∫ø -->
      <div class="vine-line"></div>
      
      <!-- ÂéüÂßãÊûúÂÆûÔºàÂâç5‰∏™Ôºâ -->
      <div
        v-for="(trip, index) in originalTrips"
        :key="trip.slug"
        :class="['fruit', `fruit-${index + 1}`]"
        @click="goToDetail(trip.slug)"
      >
        <span class="icon">{{ getIcon(index) }}</span>
        <div class="info">
          <div class="trip-title">{{ trip.name }}</div>
          <div class="desc">{{ trip.description }}</div>
          <div class="stats">
            <span class="stat-item">üëÅÔ∏è {{ trip.stats.views }}</span>
            <span class="stat-item">‚ù§Ô∏è {{ trip.stats.likes }}</span>
            <span class="stat-item">üí¨ {{ trip.stats.comments_count }}</span>
          </div>
        </div>
      </div>
      
      <!-- Êñ∞Â¢ûÁöÑÊûúÂÆûÔºàÂ∑¶Âè≥ÊéíÂàóÔºâ -->
      <div
        v-for="(trip, index) in newTrips"
        :key="trip.slug"
        :class="['fruit', 'fruit-new', index % 2 === 0 ? 'fruit-new-left' : 'fruit-new-right']"
        @click="goToDetail(trip.slug)"
      >
        <span class="icon">{{ getIcon(index + 5) }}</span>
        <div class="info">
          <div class="trip-title">{{ trip.name }}</div>
          <div class="desc">{{ trip.description }}</div>
          <div class="stats">
            <span class="stat-item">üëÅÔ∏è {{ trip.stats.views }}</span>
            <span class="stat-item">‚ù§Ô∏è {{ trip.stats.likes }}</span>
            <span class="stat-item">üí¨ {{ trip.stats.comments_count }}</span>
          </div>
        </div>
      </div>
      
      <!-- Êï¨ËØ∑ÊúüÂæÖ -->
      <div class="fruit fruit-coming-soon" style="pointer-events: none;">
        <span class="icon">‚è≥</span>
        <div class="info">
          <div class="trip-title">Êï¨ËØ∑ÊúüÂæÖ</div>
          <div class="desc">Êõ¥Â§öÁ≤æÂΩ©Ë°åÁ®ãÂç≥Â∞Ü‰∏äÁ∫øÔºåÊ¨¢ËøéÊåÅÁª≠ÂÖ≥Ê≥®ÔºÅ</div>
          <div class="date">Coming soon...</div>
        </div>
      </div>
