    <!-- È°µËÑö -->
    <div class="footer">
      &copy; 2025 ÊóÖË°åËÆ°ÂàíÂπ≥Âè∞ | ÊóÖË°åÁ§æÂå∫<br>
      ÊäÄÊúØÊîØÊåÅÔºöVue 3 + Django REST Framework
    </div>
  </div>
