<script>
import { ref, onMounted, computed } from 'vue'
import { useRouter } from 'vue-router'
import { getTripList } from '@/api/trip'
import NavBar from '@/components/NavBar.vue'

export default {
  name: 'TripListView',
  
  components: {
    NavBar
  },
  
  setup() {
    const router = useRouter()
    const trips = ref([])
    const loading = ref(true)
    
    const fetchTrips = async () => {
      loading.value = true
      try {
        const data = await getTripList()
        trips.value = data.results || data || []
      } catch (error) {
        console.error('Ëé∑ÂèñÊóÖË°åÂàóË°®Â§±Ë¥•:', error)
      } finally {
        loading.value = false
      }
    }
    
    const goToDetail = (slug) => {
      router.push(`/trip/${slug}/`)
    }
    
    // Ê†πÊçÆÁ¥¢ÂºïËøîÂõû‰∏çÂêåÁöÑÂõæÊ†á
    const getIcon = (index) => {
      const icons = ['üèñÔ∏è', 'üåä', 'üèôÔ∏è', 'üåÑ', 'üåá']
      return icons[index] || 'üó∫Ô∏è'
    }
    
    // Â∞ÜtripsÂàÜÊàêÂéüÂßãÂíåÊñ∞Êï∞ÊçÆ
    const originalTrips = computed(() => trips.value.slice(0, 5))
    const newTrips = computed(() => trips.value.slice(5))
    
    onMounted(() => {
      fetchTrips()
    })
    
    return {
      trips,
      originalTrips,
      newTrips,
      loading,
      goToDetail,
      getIcon
    }
  }
}
</script>

